.speakers{
    height: 100vh;
    display: flex;
}

.speakers-title{
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 40px;
    color: white;
    padding: 1em;
}
@media(max-width:640px){
    .speakers-title{
        font-size: 30px;
    }
}